.project {
    width: 80%;
    height: 350px;
    background-color: white;
    border-radius: 20px;
    box-shadow: 3px 3px 20px 10px rgba(0, 0, 0, 0.1);
    margin: 60px;

    display: flex;
    justify-content: stretch;
    align-items: center;
}

.projectContainer .left {
    align-self: flex-start;
    flex-direction: row;
}

.projectContainer .right {
    align-self: flex-end;
    flex-direction: row-reverse;
}

.projectImageWindowOutline {
    width: 450px;
    height: 450px;
    border-radius: 1000px;
    border: 1px solid #00083c;
    
    box-shadow: 3px 3px 20px 10px rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.projectImageWindowSqueeze {
    width: 300px;
}

.projectImageWindow {
    width: 420px;
    height: 420px;
    border-radius: 1000px;
    background-color: #00083c;
    flex-shrink: 1;
    /* flex-basis: 0; */
    
}

.project .lefty {
    transform: translateX(-42%);
}

.project .righty {
    transform: translateX(10%);
}

.projectContent {
    flex-grow: 1;
    height: 90%;
    margin: 10px;
    box-sizing: border-box;
    padding: 20px;
    /* background: red; */
}

.projectContent .title{
    font-size: 24px;
    font-family: typograph-semibold;
    text-decoration: none;
}

.projectContent .subtitleWrapper {
    margin-top: 16px;
    display: flex;
    justify-content: flex-end;
    align-items: baseline;
}

.projectContent .subtitle {
    font-size: 18px;
    font-family: typograph-light;

}

.projectContent .dates {
    font-size: 14px;
    font-family: typograph-light-italic;
    color: #00083c;

}

.projectContent .description {
    margin-top: 12px;
    font-size: 14px;
    font-family: quando;
}
