/* General section styles */
.section {
    position: relative;
    width: 100%;
    height: auto;
    background: #ffffff;
    top: -15px;
    padding-bottom: 200px;
}

.sectionContent {
    padding-bottom: 50px;
    min-width: 700px;
    max-width: 60%;
    margin: auto;
}

.sectionTitle {
    width: 800px;
    margin: auto;
    text-align: center;
    font-family: typograph-extrabold;
    font-size: 40px;
    color: #00083c

}

.sectionSubtitle {
    width: 100%;
    font-size: 24px; 
    font-family: typograph-semibold-italic;
    text-align: center;
    margin-top: 40px;
    margin-bottom: 40px;
    color: #53587a
}

/* About Me section styles */
.aboutMeImageOutline {
    background: linear-gradient(#bac0ff, #fb8686);
    width: 340px;
    height: 340px;
    border-radius: 1000px;
    margin: 40px auto;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center; 

    box-shadow: 3px 3px 20px 10px rgba(0, 0, 0, 0.1);
    
}

.aboutMeImage {

    width: 300px;
    height: 300px;
    margin: auto;
    border-radius: 1000px;
    background-image: url("../static/profile.jpeg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;

    
}

.aboutMeDescription {
    background-color: white;
    width: 100%;
    border-radius: 20px;
    box-shadow: 3px 3px 20px 10px rgba(0, 0, 0, 0.1);
    margin: auto;
    height: auto;
    box-sizing: border-box;
    padding: 20px;
    margin-bottom: 10px;

    font-size: 20px;
    font-family: typograph-light;
}


/* Experience section styles */
.resumeItemContainer {
    position: relative;
    width: 100%;
    height: auto;
}

/* Research and Projects section styles */
.projectContainer {
    margin-top: 60px;
    display: flex;
    flex-flow: column;
    justify-content: space-evenly;

}

/* Education section styles */
.educationContainer {
    display: flex;
    flex-flow: row;
    width: 100%;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin-top: 80px;
}