

.headerContainer {
    height: 1250px;
}

.header {
    height: 750px;
    /* position: relative; */
}

.canvasContainer {

    width: 100%;
    height: 100%;
    position: relative;
}

#threeCanvas {
    width: 100%;
    height: 100%;
}

.navbar {
    position: fixed;
    width: 100%;
    height: 60px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    z-index: 3;
    mix-blend-mode: difference;

    background-color: rgba(255, 255, 255, 0.0);
    transition-property: background-color mix-blend-mode color;
    transition-duration: 1s;
}

.scrolledDown {
    color: white;
    background-color: rgba(0, 0, 0, 0.6);
    transition-property: background-color mix-blend-mode color;
    transition-duration: 1s;
    mix-blend-mode: normal;
}

.mainheader{
    position: absolute;
    z-index: 3;
}


.navElement {
    display: block;
    color: #f2f2f2;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    /* width: 120px; */
    flex-basis: 150px;
    max-width: 150px;
    flex-grow: 1;
    
    height: 60px;
    line-height: 60px;
    
    
    border-top: 1px solid white;
    border-bottom: 1px solid white;
    border-right: 1px solid white;
    font-family: typograph-light;
}



.navbar a:first-child  { 
    border-left: 1px solid white;
}

.navHeader {
    /* width: 320px; */
    font-family: tourmaline-lines;
    font-size: 40px;
    text-align: left;
    padding-left: 30px;
    width: 400px;
    min-width: 400px;
}

.navEnd {
    max-width: none;
}

.mainTitle {
    width: 800px;
    height: 185px;
    margin: auto;
    text-align: center;
    font-size: 8em;
    transform: translateY(-0.7em);
    color:rgb(255, 255, 255);
    mix-blend-mode: difference;
    z-index: 10;
    /* filter:invert(100%); */

    font-family: cocogoose-regular;
    position: relative;
}

.mainSubtitle {
    margin: auto;
    width: 100%;
    height: 64px;
    font-size: 4em;
    text-align: center;
    transform: translateY(-1em);
    background: linear-gradient(#ffffff, #eaf0fd);
    font-family: typograph-light;
    position: relative;
    padding-bottom: 300px;
}

